import { ContainerModule, interfaces } from 'inversify'
import {
  APISymbol,
  bindService,
  CustomZodSchemaSymbol,
  PrismaZodSchemaSymbol,
  ServiceSymbol,
} from '@flowda-projects/flowda-shared'
import { zt } from '@flowda-projects/prisma-wms'
import * as schema from './lib/schema'
import { TaskService } from './services/task.service'
import { UserService } from './services/user.service'
import { CustomService } from './services/custom.service'
import { AssetsService } from './services/assets.service'
import type { AxiosInstance } from 'axios'
import { axiosApiInstance } from './axios'
import { DynamicTableDefService } from './services/dynamic-table-def.service'
import { DynamicTableDataService } from './services/dynamic-table-data.service'

export const wmsServiceModule = new ContainerModule((bind: interfaces.Bind) => {
  bind(PrismaZodSchemaSymbol).toConstantValue(zt)
  bind(CustomZodSchemaSymbol).toConstantValue(schema)
  bind<AxiosInstance>(APISymbol).toConstantValue(axiosApiInstance)

  bindService(bind, ServiceSymbol, TaskService)
  bindService(bind, ServiceSymbol, UserService)
  bindService(bind, ServiceSymbol, CustomService)
  bindService(bind, ServiceSymbol, AssetsService)
  bindService(bind, ServiceSymbol, DynamicTableDefService)
  bindService(bind, ServiceSymbol, DynamicTableDataService)
})
