import { Body, Controller, Get, Post, Query, Req, UseGuards } from '@nestjs/common'
import {
  CreateIncomingInspectionRecordSchemaDto,
  CreateOperationInspectionRecordSchemaDto,
  CustomService,
  SetRepairRecordDoneSchemaDto,
  UserService,
  WfAddRepairRecordSchemaDto,
  WfSetRepairRecordSchemaDto,
} from '@flowda-projects/wms-services'
import * as express from 'express'
import { UserJwtAuthGuard } from '../user/userJwtAuth.guard'

@Controller('/apps')
@UseGuards(UserJwtAuthGuard)
export class AppController {
  constructor(private readonly user: UserService, private readonly custom: CustomService) {}

  @Get('/__hi')
  hi() {
    return { hi: 'world' }
  }

  @Get('/findByUsername')
  findByUsername(@Query('username') username: string) {
    return this.user.findByUsername(username)
  }

  @Get('/getProductLineAndEquipment')
  getProductLineAndEquipment() {
    return this.custom.getProductLineAndEquipment()
  }

  @Get('/getEquipmentList')
  getEquipmentList(@Req() req: express.Request) {
    return this.custom.getEquipmentList(req.user, req.query)
  }

  @Get('/getEquipment')
  getEquipment(@Req() req: express.Request) {
    return this.custom.getEquipment(req.user, req.query)
  }

  @Get('/_test')
  test() {
    return this.custom.test()
  }

  @Post('/createIncomingInspectionRecord')
  createIncomingInspectionRecord(@Body() dto: CreateIncomingInspectionRecordSchemaDto) {
    return this.custom.createIncomingInspectionRecord(dto)
  }

  @Post('/createOperationInspectionRecord')
  createOperationInspectionRecord(@Body() dto: CreateOperationInspectionRecordSchemaDto) {
    return this.custom.createOperationInspectionRecord(dto)
  }

  @Post('/createTomorrowEquipmentRepairPlan')
  updateEquipmentCron() {
    return this.custom.createTomorrowEquipmentRepairPlan()
  }

  @Post('/setRepairRecordDone')
  setRepairRecordDone(@Body() dto: SetRepairRecordDoneSchemaDto) {
    return this.custom.setRepairRecordDone(dto)
  }

  @Post('/wfAddRepairRecord')
  wfAddRepairRecord(@Body() dto: WfAddRepairRecordSchemaDto, @Req() req: any) {
    return this.custom.wfAddRepairRecord(dto, req.user)
  }

  @Post('/wfSetRepairRecord')
  wfSetRepairRecord(@Body() dto: WfSetRepairRecordSchemaDto, @Req() req: any) {
    return this.custom.wfSetRepairRecord(dto, req.user)
  }
}
