import { Body, Controller, UploadedFile, UseInterceptors, Post, Put, Req, UseGuards } from '@nestjs/common'
import { FileInterceptor } from '@nestjs/platform-express';

import * as express from 'express'
import { UserJwtAuthGuard } from '../user/userJwtAuth.guard'
import {
  AssetsService,
} from '@flowda-projects/wms-services'
@Controller('/assets')
@UseGuards(UserJwtAuthGuard)
export class AssetsController {
  constructor(private readonly service: AssetsService) {}

  @Post('upload')
  @UseInterceptors(FileInterceptor('file'))
  async post(@Req() req: express.Request, @Body() values: any, @UploadedFile() file: any) {
    return this.service.upload(file);
  }

}
