  async setRepairRecordDone(dto: SetRepairRecordDoneSchemaDto) {
    this.logger.debug!(`[setRepairRecordDone] dto: ${JSON.stringify(dto)}`)
    const count = await this.prisma.repairRecord.updateMany({
      where: {
        id: {
          in: dto.ids,
        },
      },
      data: {
        status: db.RepairRecordStatus.DONE,
      },
    })
    return {
      count,
    }
  }

  async wfAddRepairRecord(dto: WfAddRepairRecordSchemaDto, reqUser: any) {
    this.logger.debug!(`wfAddRepairRecord, dto: ${JSON.stringify(dto)}`)
    // todo:  API  API 
    const rrRet = await this.prisma.repairRecord.create({
      data: {
        type: db.RepairType.UNSCHEDULE,
        equipmentId: dto.id,
        status: db.RepairRecordStatus.TO_ASSIGN,
        description: '',
      },
    })
    // 
    const processDefKey = 'Process_addRepairRecord'
    try {
      await this.task.start(
        {
          tenantId: 'quarkdev',
          processDefKey,
          businessKey: rrRet.id, // todo: 
        },
        reqUser,
      )
    } catch (e: any) {
      // 
      // todo: 
      this.logger.error(
        `[wfAddRepairRecord] start process ${processDefKey} failed, businessKey ${rrRet.id}, error ${e.message}, rollback`,
      )
      // 
      await this.prisma.repairRecord.delete({
        where: { id: rrRet.id },
      })
    }

    return {
      success: true,
    }
  }

  async wfSetRepairRecord(dto: WfSetRepairRecordSchemaDto, reqUser: any) {
    this.logger.debug!(`wfSetRepairRecord, dto: ${JSON.stringify(dto)}`)
    // todo:  API  API 
    const orig = await this.prisma.repairRecord.findUnique({
      where: {
        id: dto.id,
      },
    })
    if (orig == null) {
      throw new Error(`[wfSetRepairRecord] cannot find repairRecord, ${dto.id}`)
    }
    const ret = await this.prisma.repairRecord.update({
      where: {
        id: dto.id,
      },
      data: {
        status: db.RepairRecordStatus.DONE,
      },
    })
    try {
      await this.task.complete(dto.taskId)
    } catch (e: any) {
      this.logger.error(`[wfSetRepairRecord] complete task ${dto.taskId} failed, error ${e.message}, rollback`)
      await this.prisma.repairRecord.update({
        where: {
          id: dto.id,
        },
        data: {
          status: orig.status,
        },
      })
    }

    return {
      success: true,
    }
  }
